.home{
    border: 1px solid cyan;
    overflow:auto;
}

.home img {
    width:100%;
}

.main-header{
    position: fixed;
    z-index:99;
    width:100%;
    height:65px;
    /* border-bottom: -10px; */
}

.logo-search-header{
    padding-top:80px;
}
.login-nav-bar{
    padding-top:15px;
}
.item-container img{
    margin-top:15px;
    max-width:250px;
}

.item-container{
    background-color: rgba(73, 114, 109, 0.8);
    padding-top:2vh;
    color: #eee;
}

.item-container img, h1,h2,h3,h4,h5,h6,p,#email{
    display:block;
    margin-top:35px;
    margin-left:auto;
    margin-right:auto;
    margin-bottom:35px;
    /* width:30vw; */
    border-radius: 8px;
    text-align:center;
    text-decoration: none;
}

.logo{
    border-radius:2px;
    height:70px;
}

.finePrint{
    font-size: 9px;
}

.carousel-container{
    max-height:700px;
    overflow:hidden;
}


.logo-search-header img{
    height: 90px;
}

.header{
    color: #969696
}

.login-nav-bar{
    height: 35px;
}
.header .login-nav-bar{
    height: 30px;
}
.header .btn-github{
    background-image: url("/images/GitHub-Mark-Light-32px.png") !important;
    width: 210px !important;
    color: #fff;
    background-color: #444;
    border-color: rgba(0,0,0,0.2);
    background-repeat: no-repeat;
    background-position: 5px;
    text-align: right;
}
.btn-github:hover{
    color: rgba(200,200,200,0.5);
}

.mainLinks{
    margin-left: 5px;
    margin-right: 5px;
}
.game-container{
    background:#fff;
    padding:35px 20px 20px;
    margin:50px 0;
}
.game-container h1{
    font-family:'ZCOOL QingKe HuangYou',cursive;
    font-size:5rem;
}
.game-pic{
    width:95%;
}
.game-title, .game-desc{
    margin:0;
}
@media screen and (max-width:600px){
    .game-pic{
        width:80%;
    }
    .game-title{
        margin-top:10px;
    }
}
@media screen and (min-width:600px) and (max-width:1200px){
    .game-desc{
        width:80%;
        margin:0 auto;
    }
}
html{
    background-color:#004d40;
    overflow:auto;
    /* overflow:hidden; */
    height:100vh;
    font-family: 'Simplifica';
    letter-spacing:1.5px
}

@font-face {
    font-family:"Simplifica";
    src: url(/static/media/Simplifica.22dd36f6.ttf) format("truetype");
  }

body{
    /* margin-top:30px; */
    /* overflow: auto; */
    background-image: -webkit-linear-gradient(#b9bebe00,#004d40), url('/images/background_images/main_background.png');
    background-image: linear-gradient(#b9bebe00,#004d40), url('/images/background_images/main_background.png');
    display:fixed;
    background-size:cover;
    height:120vh;
}

.underConstruction{
    font-size: 5vw;
}


@-webkit-keyframes App-logo-spin{
    from{
        -webkit-transform:rotate (0 deg);
                transform:rotate (0 deg)
    }
    to{
        -webkit-transform:rotate (360 deg);
                transform:rotate (360 deg)
    }
}


@keyframes App-logo-spin{
    from{
        -webkit-transform:rotate (0 deg);
                transform:rotate (0 deg)
    }
    to{
        -webkit-transform:rotate (360 deg);
                transform:rotate (360 deg)
    }
}
